
.logo-img{
    height: 110px;
    width: 110px;
}
.app{
    width: 100%;
    height: 100%;
}
.header{
    display: flex;
    width: 100%;
    justify-content: space-between;
    box-sizing: border-box;
    background-color: rgb(255, 255, 255);
    align-items: center;
    padding: 0px 70px;

}
.nav-ul{
    display: flex;
    list-style-type: none;
    padding-top: 0;
    /* margin-top: 20px; */
    font-size: 18px;
    padding-right: 200px;
    align-items: center;
    justify-content: center;
}
.nav-ul > li{
    padding: 10px 40px;
    margin: 8px;  
    color: rgb(36, 36, 36);
}
.search{
    padding: 10px;
}
.res-card{
    width: 300px;
    height: 370px;
    /* border: 1px solid black; */
    border-radius: 10px;
    background-color: white;
    margin: 5px;
    font-family: sans-serif;
}
.res-card:hover{
    transform: scale(1.05);
    transition-duration: 400ms;
}
.res-sample{
    width: 100%;
    height: 70%;
    border-radius: 10px;
}
.cardInfo{
    margin-top: 5px;
}
.res-card > .cardInfo > h3{
    margin: 0;
    padding-left: 5px;
    font-size: 17px;
    font-weight: 500;
    color: rgba(2, 6, 12, 0.75);
}
.res-card > .cardInfo > .ratingEta >h4{
    margin: 0;
    font-size: 17px;
    color: rgb(71, 71, 71);
    font-weight: 500;
}
.res-card > .cardInfo > .ratingEta >p{
    margin: 0;
    font-size: 15px;
    color: rgb(56, 56, 56);
}
.res-card > .cardInfo > p{
    margin: 0;
    padding-left: 5px;
    font-size: 14px;
    font-weight: 100;
    word-break: break-all;
    white-space: normal;
    color: grey;
}
.res-card > h4{
    margin-top: 0px;
    margin-bottom: 5px;
    padding-left: 5px;
    word-break: break-all;
    white-space: normal;
}
.ratingEta{
    display: flex;
    margin-left: 5px;
}
.res-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly
}
.cardInfo{
    display: flex;
    flex-direction: column;
}
.star{
    color: green;
    padding-right: 3px;
}